import moment from "moment";

export const alphabe = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J"];

export const formatFullDate = (date) => {
  return `Th ${convertDay(moment(date).format('d'))}, ${moment(date).format("DD")} thng ${moment(date).format(
    "MM"
  )}, ${moment(date).format("YYYY")}`;
};

export const formatTime = (time, timeAdd) => {
  if (timeAdd) {
    return moment(time).add(timeAdd, 'hours').format('hh:mm');
  } else {
    return moment(time).format('hh:mm');
  }
}

export const convertDay = (dayNumber) => {
  switch (dayNumber) {
    case '0':
        return "Hai";
    case '1':
      return "Ba";
    case '2':
      return "T";
    case '3':
      return "Nm";
    case '4':
      return "Su";
    case '5':
      return "By";
    case '6':
      return "Ch nht";
    default:
      return "";
  }
};
